@import "../common/mixins";
@import "../common/variable";

.home-hero {
  position: relative;
  padding-bottom: r(50);
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
    display: block;
    height: r(250);
    width: 100%;
    background-color: #F7F7F7;
  }
  &__container {}
  &__display {
    position: relative;
    z-index: 2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    min-height: r(800);
    padding: r(15);
    -o-border-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)) 1;
       border-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.4)), to(rgba(0,0,0,0.4))) fill 1;
       border-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)) fill 1;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &__main {
    position: relative;
    z-index: 2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    max-width: r(845);
    text-align: center;
    line-height: 1;
    color: $colorWhite;
  }
  &__title {
    font-weight: bold;
    font-size: r(83);
    @include max-tablet {
      font-size: r(48);
    }
  }
  &__description {
    max-width: r(516);
    margin-top: r(50);
    font-size: r(27);
    line-height: 1;
  }
  &__action {
    margin-top: r(50);
  }
}

.home-hero_short {
  .home-hero {
    &__display {
      min-height: r(400);
    }
  }
}

.home-hero + section {
  background-color: #F7F7F7;
}